import { Component, OnInit } from '@angular/core';
import { RequestsService } from '../../logInAndSignupService/requests.service';
import { DataRequestsService } from '../../request-to-BE/data-requests.service';
import { NgForm } from '@angular/forms';
// import { userInfo } from 'os';
// import { ValueAccessor } from '@ionic/angular/directives/control-value-accessors/value-accessor';

@Component({
  selector: 'app-modal-page',
  templateUrl: './modal-page.page.html',
  styleUrls: ['./modal-page.page.scss'],
})
export class ModalPagePage implements OnInit {
  update = {
    newUser: {
      Lastname: '',
      Firstname: '',
      Birthday: '',
      Age: null,
      Address: '',
      Marital_status: '',
      Email: '',
      Contact_number: null,
      Facebook: '',
      Instagram: '',
      Twitter: '',
      Category: '',
    }, groupBelong: {
      Leader: ''
    }, role: {
      code: 'Member'
    }
  };

  constructor(
    private request: RequestsService,
    private datasRequest: DataRequestsService
  ) { }

  ngOnInit() {
    this.request.getTheCurrentUserIdInStorage().then(res => {
      this.datasRequest.getTheCurrentUser({userID: res}).subscribe(data => {
        this.update.newUser = data[0]
        console.log(this.update.newUser)
      })
    })
  }
  

  onUpdate(form: NgForm) {
    this.request.signUp(this.update.newUser).subscribe(res => {
      // console.log(res)
      // this.router.navigate(['/app/tabs/cshedule'])
    })
  }
}
